.header {
  background-color: #2c2c2c;
  height: 800px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.logo {
  width: 44px;
  height: 25px;
}

.nav {
  display: flex;
  padding: 48px 0;
  justify-content: center;
  gap: $screen_width / 10.1;
  align-items: center;
  @media (max-width: 860px) {
    display: flex;
    padding: 8px;
    gap: $screen_width / 1.44;
  }
  &__container {
    display: flex;
    gap: 48px;
    @media (max-width: 860px) {
      display: none;
    }

    &--1 {
      @media (min-width: 860px) {
        display: none;
      }
      display: flex;
      align-items: center;
      gap: 10px;
    }

    &_link {
      position: relative;
    }

    &_item {
      font-size: 13px;
      color: #fff;

      &:hover::after {
        content: "";
        display: block;
        position: absolute;
        width: 100%;
        height: 2px;
        border-radius: 4px;
        background-color: #2060f6;
      }
    }
  }
}

.burger-btn {
  width: 18px;
  height: 8px;
}

.far-link {
  font-size: 13px;
  font-weight: 700;
  color: #fff;
  text-transform: uppercase;
  &::after {
    content: "";
    display: block;
    position: absolute;
    width: 47px;
    height: 2px;
    border-radius: 4px;
    background-color: #2060f6;
  }
}
